.toDo {
    overflow-y: auto;
    width: 400px;
    height: 600px;
    background-color:  #248d9b81;
    border-radius: 5px;
    margin: 0 auto;
    -webkit-box-shadow: 13px 9px 26px 1px rgba(255,255,255,1);
    -moz-box-shadow: 13px 9px 26px 1px rgba(255,255,255,1);
    box-shadow: 13px 9px 26px 1px rgba(255,255,255,1);
    padding-top: 20px; }
.count {
    display: block;
    margin: 0 auto;
    width: 20px;
    height: 50px;
    font-size: 30px;
    color: #ffffff; }
.formStyle {
    display: flex;
    flex-direction: column;
    align-items: center;
    & input {
        font-size: 15px;
        text-align: center;
        height: 30px;
        width: 350px;
        border-radius: 5px;
        border: none;
        color: #000000;
        text-indent: 0px;
        font-weight: bold;
        &::placeholder {
            color: #000000;
            font-size: 17px; }
        &:focus {
            text-align: left;
            &::placeholder {
                color: #000000;
                text-indent: 500px;
                transition: text-indent 0.3s ease; } } }
    & button {
        margin-top: 10px;
        background-color: #ffffff;
        height: 30px;
        width: 350px;
        border-radius: 5px;
        border: none;
        font-size: 17px;
        font-weight: bold;
        color: #000000; } }

ul {
    display: flex;
    flex-direction: column;
    padding: 0;
    margin: 0;
    width: 100%;
    align-items: center; }
.toDoItem {
    display: flex;
    list-style: none;
    margin-top: 20px;
    width: 350px;
    min-height: 30px;
    border-radius: 5px;
    background-color: #ffffff;
    justify-content: space-between;
    align-items: center;
    word-break: break-word;
    word-wrap: break-word;
    .btn-trash {
        display: block;
        height: 31px;
        width: 31px;
        border: none; } }

